body {
  margin: 0;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    167deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 33%,
    rgba(0, 212, 255, 1) 100%
  );
}

.container {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.15);
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 20px 5px rgba(0, 0, 0, 0.438);
}

h1 {
  margin: 10px 0 20px;
}

.city-loc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.weather-icon {
  display: inline-block;
}

span.temp {
  font-size: 4rem;
}

.city {
  font-size: 1.3rem;
}

input {
  background-color: rgba(255, 255, 255, 0.247);
  border: none;
  padding: 10px;
  border-radius: 10px;
  color: #fff;
  font-weight: bold;
  font-size: 18px;
}

input:focus {
  outline: none;
}

input::placeholder {
  color: rgba(255, 255, 255, 0.658);
  font-weight: 400;
  font-style: italic;
}

button {
  padding: 7px;
  border-radius: 10px;
  border: none;
  background-color: #fff;
  font-weight: bold;
  font-size: 1rem;
}

.temp::first-letter {
  text-transform: uppercase;
}

form {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-bottom: 30px;
}

.arrows {
  height: 15px;
  width: 15px;
}
